        style="overflow:hidden;border:#ddd 1px solid;"
        allowtransparency="true"
        frameborder="0"
      ></iframe>
    </div>

    <div v-if="false">
      <!-- Using components -->
      <b-card bg-variant="light">
        <b-form-group
          label-cols-lg="3"
          label="Profile"
          label-size="lg"
          label-class="font-weight-bold pt-0"
          class="mb-0"
        >
          <b-form-group
            label-cols-sm="3"
            label="你的名字"
            label-align-sm="right"
            label-for="nested-name"
            :invalid-feedback="invalidName"
          >
            <b-form-input id="nested-name" v-model="name" :state="checkName"></b-form-input>
          </b-form-group>

          <b-form-group label-cols-sm="3" label="與新人關係:" label-align-sm="right" class="mb-0">
            <b-form-radio-group class="pt-2" :options="['男方', '女方']" @change="checkShowRelation"></b-form-radio-group>
            <b-form-radio-group
              class="pt-2"
              :options="['同學', '親戚', '同事', '其他']"
              v-show="isShowRelation"
            ></b-form-radio-group>
          </b-form-group>

          <b-form-group label-cols-sm="3" label="是否需要喜帖:" label-align-sm="right" class="mb-0">
            <b-form-radio-group
              class="pt-2"
              :options="['電子喜帖', '紙本喜帖', '不需要喜帖']"
              @change="checkShowInvitation"
            ></b-form-radio-group>
          </b-form-group>

          <b-form-group
            label-cols-sm="3"
            label="E-mail"
            label-align-sm="right"
            label-for="nested-email"
            :invalid-feedback="invalidEmail"
            v-if="invitationtype === 0"
          >
            <b-form-input id="nested-email" v-model="email" :state="checkEmail"></b-form-input>
          </b-form-group>

          <b-form-group
            label-cols-sm="3"
            label="地址"
            label-align-sm="right"
            label-for="nested-address"
            :invalid-feedback="invalidAddress"
            v-if="invitationtype === 1"
          >
            <b-form-input id="nested-address" v-model="address" :state="checkAddress"></b-form-input>
          </b-form-group>

          <b-form-group label-cols-sm="3" label="是否出席:" label-align-sm="right" class="mb-0">
            <b-form-radio-group
              class="pt-2"
              :options="['一定到場祝福', '那天有事情了']"
              @change="checkShowPresent"
            ></b-form-radio-group>
          </b-form-group>
        </b-form-group>
      </b-card>

      <b-card bg-variant="light" v-show="isPresent">
        <b-form-group
          label-cols-lg="3"
          label="Attention"
          label-size="lg"
          label-class="font-weight-bold pt-0"
          class="mb-0"
        >
          <b-form-group label-cols-sm="3" label="出席人數:" label-align-sm="right" class="mb-0">
            <b-form-radio-group
              class="pt-2"
              :options="['1', '2', '3', '4', '5', '以上']"
              @change="checkNumberPresent"
            ></b-form-radio-group>
          </b-form-group>
          <b-form-group label-cols-sm="3" label="兒童座椅:" label-align-sm="right" class="mb-0">
            <b-form-radio-group
              class="pt-2"
              :options="['1', '2', '3', '4', '5', '以上']"
              @change="checkNumberChild"
            ></b-form-radio-group>
          </b-form-group>
          <b-form-group label-cols-sm="3" label="素食人數:" label-align-sm="right" class="mb-0">
            <b-form-radio-group
              class="pt-2"
              :options="['1', '2', '3', '4', '5', '以上']"
              @change="checkNumberVege"
            ></b-form-radio-group>
          </b-form-group>
          <b-form-group
            label-cols-sm="3"
            label="連絡電話:"
            label-align-sm="right"
            label-for="nested-phone"
            :invalid-feedback="invalidPhone"
          >
            <b-form-input id="nested-phone" v-model="phone" :state="checkPhone"></b-form-input>
          </b-form-group>
        </b-form-group>
      </b-card>

      <b-card
