      >
        <b-card-text>如果你有一些祝福的話不好意思當面說, 就在這裡留言給我們吧!</b-card-text>
        <b-form-textarea
          id="textarea"
          v-model="blessingArea"
          placeholder="Enter something..."
          rows="3"
          max-rows="6"
          class="mb-4"
        ></b-form-textarea>
        <b-button href="#" variant="primary">送出</b-button>
      </b-card>

      <b-card
        title="非常感謝您的填寫:"
        img-src="https://picsum.photos/600/300/?image=30"
        img-alt="wedding"
        img-top
        class="mb-2"
      >
        <b-card-text>若有任何問題或狀況，都歡迎隨時與我們聯繫</b-card-text>
        <b-card-text>期待婚禮當天與你相見, Cheers!</b-card-text>
        <b-card-text>Deka & Evelyn</b-card-text>
      </b-card>
    </div>
  </div>
</template>

<script>
import { type } from "os";
export default {
  name: "Attention",
  props: {
    msg: String
  },
  data() {
    return {
      //form part
      name: "",
      isShowRelation: false,
      invitationtype: 3,
      email: "",
      address: "",
      isPresent: false,
      numberpresent: 0,
      numberchild: 0,
      numbervege: 0,
      phone: "",
      blessingArea: "",

      inputState: {
        name: false,
        relation: false,
        present: false,
        email: false,
        address: false,
        numberpresent: false,
        numberchild: false,
        numbervege: false,
        phone: false
      }
    };
  },
  computed: {
    checkName() {
      let vm = this;
      var state = false;
      state = vm.name.length > 0 ? true : false;
      vm.inputState.name = state;

      return state;
    },
    invalidName() {
      let vm = this;
      if (vm.inputState.name == true) {
        return "";
      } else {
        return "請輸入名字";
      }
    },
    checkEmail() {
      let vm = this;
      //Regular expression Testing
      var emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
      var isValid = false;
      if (vm.email.search(emailRule) != -1) {
        isValid = true;
      } else {
        isValid = false;
      }
      vm.inputState.email = isValid;
      return isValid;
    },
    invalidEmail() {
      let vm = this;
      if (vm.inputState.email == true) {
        return "";
      } else {
        return "請輸入email: eg. dekacompt@gmail.com";
      }
    },
    checkAddress() {
      let vm = this;
      var isValid = false;
      if (vm.address.length > 0) {
        isValid = true;
      } else {
        isValid = false;
      }
      vm.inputState.address = isValid;
      return isValid;
    },
    invalidAddress() {
      let vm = this;
      if (vm.inputState.address == true) {
        return "";
      } else {
        return "請輸入地址: eg. 801 高雄市前金區成功一路266號";
      }
    },
    checkPhone() {
      let vm = this;
      //Regular expression Testing
      var phoneRule = /^09[0-9]{2}-[0-9]{6}$/;
      var isValid = false;
      if (vm.phone.search(phoneRule) != -1) {
        isValid = true;
      } else {
        isValid = false;
      }
      vm.inputState.phone = isValid;
      return isValid;
    },
    invalidPhone() {
      let vm = this;
      if (vm.inputState.address == true) {
        return "";
      } else {
        return "請輸入手機: eg. 09xx-xxxxxx";
      }
    }
  },
  methods: {
    checkShowRelation() {
      let vm = this;
      vm.isShowRelation = true;
    },
    checkShowPresent(value) {
      let vm = this;
      vm.isPresent = value === "一定到場祝福" ? true : false;
    },
    checkShowInvitation(value) {
      let vm = this;
      var type = 2;
      if (value === "電子喜帖") {
        type = 0;
      } else if (value === "紙本喜帖") {
        type = 1;
      }
      vm.invitationtype = type;
    },
    checkNumberPresent(value) {
      let vm = this;
      if (
        value === "1" ||
        value === "2" ||
        value === "3" ||
        value === "4" ||
        value === "5"
      ) {
        vm.numberpresent = parseInt(value);
        vm.inputState.numberpresent = true;
      } else if (value === "以上") {
        vm.numberpresent = 6;
        vm.inputState.numberpresent = true;
      } else {
        vm.numberpresent = 0;
        vm.inputState.numberpresent = false;
      }
      //console.log(vm.numberpresent);
    },
    checkNumberChild(value) {
      let vm = this;
      if (
        value === "1" ||
        value === "2" ||
        value === "3" ||
        value === "4" ||
        value === "5"
      ) {
        vm.numberchild = parseInt(value);
        vm.inputState.numberchild = true;
      } else if (value === "以上") {
        vm.numberchild = 6;
        vm.inputState.numberchild = true;
      } else {
        vm.numberchild = 0;
        vm.inputState.numberchild = false;
      }
    },
    checkNumberVege(value) {
      let vm = this;
      if (
        value === "1" ||
        value === "2" ||
        value === "3" ||
        value === "4" ||
        value === "5"
      ) {
        vm.numbervege = parseInt(value);
        vm.inputState.numbervege = true;
      } else if (value === "以上") {
        vm.numbervege = 6;
        vm.inputState.numbervege = true;
      } else {
        vm.numbervege = 0;
        vm.inputState.numbervege = false;
      }
    },
    sentJson() {
      let vm = this;
      var data = {
        name: "Dekacompta",
        phone: "123",
        time: "999",
        order: "xxx",
        price: "12"
      };
      //
      console.log(data);
      vm.axios
        .get(
          "https://script.google.com/macros/s/AKfycbzye9WlpkJLOKMVUj7x88kjtdIe4OHs2OwVqNlM41i64AvIphE/exec",
          {
            params: data
          }
        )
        .then(function(response) {
          // 成功回應
          //console.log(response.data);
          //vm.listData = response.data.results
        })
        .catch(function(error) {
          // 失敗回應
          //console.log(error);
        });
    }
  }
};
</script>

